.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  
  width: 100%;
  min-height: 400px;
  height: 100%;
  margin: 20px auto 0;
  padding-bottom: 30px;

  background-color: #eeeeee;
  border-radius: 5px 5px 0 0;
  border-radius: 0 30px;
  
}

.tracker-form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;

  min-width: 320px;
  max-width: 600px;
  width: 100%;
  /* min-height: 100px; */
  gap: 30px;

  padding: 20px 20px 0;
  margin-top: 20px;

  /* background-color: #eeeeee; */
  border-radius: 5px 5px 0 0;
}

.tracker-data, .tracker-kilometers {
  display: flex;
  flex-direction: column;

  width: 150px;
}

.tracker-form label {
  padding: 5px 0;
}

.tracker-form input {
  padding: 8px 10px;

  border: 1px solid #808080;
  border-radius: 5px;
}

.tracker-form input:focus {
  outline: none;

  box-shadow: inset 0 0 2px 1px #808080;
}

.tracker-form button {
  padding: 8px 10px;

  border: 1px solid #808080;
  border-radius: 5px;

  cursor: pointer;
}

.tracker-form button:hover {
  box-shadow: 1px 1px 5px 0 #808080;
}

.button {
  min-width: 100px;
  width: 150px;
}

@media only screen and (max-width:430px) {
  .tracker-form {
    flex-direction: column;
    align-items: center;
  }
}

:root {
  --border-color: grey; /* Объявление переменной */
  --border-radius: 10px; /* Объявление переменной */
}

.tracker-table {
  min-width: 320px;
  max-width: 600px;
  width: 100%;
  /* padding: 5px; */

  text-align: center;
  
  /* border-collapse: collapse; */
  border-collapse: separate;
  border-spacing: 0;
  /* border:solid black 1px; */
  border-radius: var(--border-radius);
}

.tracker-table thead {
  height: 60px;
}

/* .tracker-list {
  min-height: 40px;
} */

.tracker-list td {
  padding: 10px;
}

/* верхняя рамка у первой строчки */
.tracker-list tr:first-child td {
  border-top: 1px solid var(--border-color);
}

/* нижняя рамка у последней строчки */
.tracker-list tr:last-child td {
  border-bottom: 1px solid var(--border-color);
}

/* левая рамка у первого столбца */
.tracker-list tr td:first-child {
  border-left: 1px solid var(--border-color);
}

/* правая рамка у последнего столбца */
.tracker-list tr td:last-child {
  border-right: 1px solid var(--border-color);
}

/* Округление углов верхнего левого и верхнего правого углов */
.tracker-list tr:first-child td:first-child {
  border-top-left-radius: var(--border-radius);
}

.tracker-list tr:first-child td:last-child {
  border-top-right-radius: var(--border-radius);
}

/* Округление углов нижнего левого и нижнего правого углов */
.tracker-list tr:last-child td:first-child {
  border-bottom-left-radius: var(--border-radius);
}

.tracker-list tr:last-child td:last-child {
  border-bottom-right-radius: var(--border-radius);
}

.tracker-button {
  cursor: pointer;
}

.tracker-button:first-child {
  margin-right: 15px;
}